				{name:'待付款',icon:'../../static/l1.png',badge:1,status:1},
				{name:'待发货',icon:'../../static/l2.png',badge:2,status:2},
				{name:'待收货',icon:'../../static/l3.png',badge:6,status:3},
				{name:'待评价',icon:'../../static/l4.png',badge:9,status:4},
				{name:'退换货',icon:'../../static/l5.png',badge:0,status:5}
			],
			severList:[
				[
					{name:'绑定买号',icon:'../../static/point.png',url:'../bindAccount/bindAccount_select'
					},
					{name:'买号列表',icon:'../../static/quan.png',url:'../accountList/accountList'
					},
					{name:'认证中心',icon:'../../static/momey.png',url:'../setting/setting'
					},
					{name:'账户安全',icon:'../../static/renw.png',url:'../security/security'
					},
					{name:'客服',icon:'../../static/renw.png',
					},
					{name:'客服2',icon:'../../static/renw.png',
					},
					{name:'账户安全2',icon:'../../static/renw.png',url:'../security/security'
					},
					{name:'客服3',icon:'../../static/renw.png',
