					]
			]
		};
	},
	onLoad() {
				if(this.hasLogin){
					uni.request({
						url:`${this.$serverUrl}/home/home?uname=`+this.userInfo.username,
						header:{
							"Content-Type": "application/x-www-form-urlencoded",
							"token":this.userInfo.token
						},
						method:"GET",
						success: (res) => {
							this.userinfo = res.data	
						}
					})
				}else{
					uni.reLaunch({
						url:"../login/login"
					})
				}
	},
	methods: {
		
		//用户点击订单类型
		toOrderType(status){
			uni.navigateTo({
				url:"../task/task?status="+status,
			})
		},
		//用户点击列表项
		toPage(url){
			uni.navigateTo({
				url:url,
			})
		}
